import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../common/Modal/Modal';
import APInput from '../common/APInput/APInput';
import APButton from '../common/APButton';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

const affiliateModalClasses = getBEMClasses('affiliate-modal');

const AffiliateModal = (props, context) => {
  const { handleSubmit, submitting } = props;

  return (
    <Modal
      isOpen={props.isOpen}
      title={context.t('Create or Edit Affiliate Tag')}
      onCancel={() => {
        props.close();
      }}
      close={props.close}
      customClass={affiliateModalClasses('ap-modal')}>
      <form onSubmit={handleSubmit} className={affiliateModalClasses('form')}>
        <div className={affiliateModalClasses('input-wraper')}>
          {context.t('Change tag')}
          <APInput
            customClass={affiliateModalClasses()}
            type="text"
            name="tag"
          />
        </div>
        <div className={affiliateModalClasses('footer')}>
          <APButton type="submit" customClass={affiliateModalClasses()}>
            {submitting ? context.t('Submitting...') : context.t('Update')}
          </APButton>
        </div>
      </form>
    </Modal>
  );
};

AffiliateModal.propTypes = {
  submitting: PropTypes.bool.isRequired,
  close: PropTypes.func.isRequired
};

AffiliateModal.contextTypes = {
  t: PropTypes.func.isRequired
};

export default AffiliateModal;



